.popup {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    visibility: hidden;
    opacity: 0;
    justify-content: center;
    align-items: center;
    transition: opacity .2s linear, visibility .2s linear;
    display: flex;
}